.bp4-icon {
  display: inline-block;
  flex: 0 0 auto;
  vertical-align: text-bottom;
}
.bp4-icon:not(:empty)::before {
  /* stylelint-disable */
  content: "" !important;
  content: unset !important;
  /* stylelint-enable */
}
.bp4-icon > svg {
  display: block;
}
.bp4-icon > svg:not([fill]) {
  fill: currentcolor;
}

